body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  background-color: #f4f4f9;
  color: #333;
}

.App {
  text-align: center;
}

.App-header {
  background-color: #1a1a2e;
  padding: 20px;
  color: white;
}

main {
  padding: 0;
}

.full-screen {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 40px;
  background-color: #fff;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  margin: 20px;
  border-radius: 8px;
}

.full-screen:nth-child(even) {
  background-color: #f4f4f9;
}

h2 {
  font-size: 2.5rem;
  margin-bottom: 20px;
}

h3 {
  font-size: 1.75rem;
  margin-bottom: 15px;
}

p, ul {
  font-size: 1.125rem;
  line-height: 1.6;
  margin-bottom: 20px;
  max-width: 800px;
  text-align: left;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  font-size: 1.125rem;
  line-height: 1.6;
  margin-bottom: 10px;
}

a {
  color: #007bff;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

.cta-button {
  display: inline-block;
  padding: 10px 20px;
  font-size: 1.125rem;
  color: white;
  background-color: #007bff;
  border: none;
  border-radius: 4px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  cursor: pointer;
  text-decoration: none;
}

.cta-button:hover {
  background-color: #0056b3;
}

form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

label {
  font-size: 1.125rem;
  margin-bottom: 10px;
}

input, textarea {
  width: 100%;
  max-width: 400px;
  padding: 10px;
  margin-bottom: 20px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

button {
  padding: 10px 20px;
  font-size: 1.125rem;
  color: white;
  background-color: #007bff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

button:hover {
  background-color: #0056b3;
}

footer {
  background-color: #1a1a2e;
  color: white;
  padding: 10px;
  position: fixed;
  width: 100%;
  bottom: 0;
}
